.Heading {
  margin: auto;
  width: "auto";
  text-align: center;
  border-bottom: 2px solid teal;
  margin-top: 400px;
}
.TabHeading {
  margin: auto;
  border-bottom: 2px solid teal;
  width: auto;
}
.MobHeading {
  margin: auto;
  width: auto;
  border-bottom: 2px solid teal;
}
.texts {
  transition: transform 0.5s linear;
}
.texts:hover {
  color: #dc143c;
  transform: scale(1.1);

  transition: 0.5s ease-in;
}
.line {
  width: 90%;
  height: 2px;
  margin: auto;
  align-items: center;
  align-self: center;
}
.boxes {
  transition: transform 0.5s linear;
}
.boxes:hover {
  transform: scale(1.1);

  transition: 0.5s ease-in;
}
